import OperationsView from './classes/OperationsView';
import ErrorView from './classes/ErrorView';
import ConfigPresenter from './ConfigPresenter';
import ConfigModel from './ConfigModel';
import AccountsPresenter from './AccountsPresenter';
import AccountsModel from './AccountsModel';
import InfoView from "./classes/InfoView";
import NavbarView from "./classes/NavbarView";

let configData;
let accountsData;
let infoData;
let container;
let infoContainer;
let configContainer;
let accountsContainer;
let receiveContainer;
let sendContainer;
let navContainer;
let operationsType;
let operationsReceiveView;
let operationsSendView;
let accountsPresenter;

export default class App {

  static showReceive() {
    operationsReceiveView = new OperationsView(operationsType, receiveContainer, accountsData);
    operationsReceiveView.onShow();
  }

  static showSend() {
    operationsSendView = new OperationsView(operationsType, sendContainer, accountsData);
    operationsSendView.onShow();
  }

  static showNavbar() {
    const navbarView = new NavbarView(navContainer);
    navbarView.onInit();
  }

  static showInfo() {
    const infoView = new InfoView(infoContainer, infoData);
    infoView.onInit();
  }

  static showConfig() {
    const configPresenter = new ConfigPresenter(new ConfigModel(configData, configContainer));
    configPresenter.onInit();
  }

  static showAccounts() {
    accountsPresenter = new AccountsPresenter(new AccountsModel(accountsData, accountsContainer, configData));
    accountsPresenter.onInit();
  }

  static refreshAccounts() {
    accountsPresenter.onUpdateConfig();
  }

  static hideReceive() {
    if(operationsReceiveView) {
      operationsReceiveView.onHide();
    }
  }

  static hideSend() {
    if(operationsSendView) {
      operationsSendView.onHide();
    }
  }

  static set config(data) {
    configData = data;
  }

  static get config() {
    return configData;
  }

  static set accounts(data) {
    accountsData = data;
  }

  static set info(data) {
    infoData = data;
  }

  static set setOperationsType(type) {
    operationsType = type;
  }

  static set container(tag) {
    container = tag;
    infoContainer = $('.jInfoContainer', container);
    configContainer = $('.jConfigContainer', container);
    accountsContainer = $('.jAccountsContainer', container);
    sendContainer = $('.jSendContainer', container);
    receiveContainer = $('.jReceiveContainer', container);
    navContainer = $('.jNavbarContainer', container);
  }

  static showConfigError() {
    const errorView = new ErrorView(configContainer);
    errorView.onInit();
  }

  static showAccountsError() {
    const errorView = new ErrorView(accountsContainer);
    errorView.onInit();
  }
}
