import ParentClass from './ParentClass';
import {Prefixes} from './type-data';
import moment from 'moment/src/moment';

export default class operationView extends ParentClass {
    constructor(operation, modal) {
        super();
        this._operation = operation;
        this._modal = modal;
    }

    setExceptionMessage() {
        $('.jExceptionMessageText', this._modal).text(this._operation.exceptionMessage);
    }

    getMarkup() {
        let date = moment(this._operation.date).format('YYYY-MM-DD HH:mm:ss');
        let exceptionMessage = '';

        if (this._operation.exceptionMessage) {
            exceptionMessage = `<a href="#" class="text-warning jExceptionMessageButton"
                         data-toggle="modal"
                         data-target="#exceptionMessageModal"><i class="fas fa-info-circle"></i></a>`
        }
        let template = `<th class="${Prefixes.OPERATION}date" scope="row" >${date}</th>
                    <td class="${Prefixes.OPERATION}period">${this._operation.period}</td> 
                    <td class="${Prefixes.OPERATION}login">${this._operation.login}</td>
                    <td class="${Prefixes.OPERATION}fileName">${this._operation.fileName}</td>
                    <td class="${Prefixes.OPERATION}fileSize">${this._operation.fileSize}</td>
                    <td class="${Prefixes.OPERATION}resultState">${this._operation.resultState}</td>
                    <td class="${Prefixes.OPERATION}requestInterval">${this._operation.resultMessage}</td>
                    <td class="${Prefixes.OPERATION}exceptionMessage">${exceptionMessage}</td>`;
        return template;
    }

    bindHandlers() {
        $('.jExceptionMessageButton', this.element)
            .on('click', () => {
                this.setExceptionMessage();
            });
    }

}
