import ConfigView from './classes/ConfigView';

export default class ConfigPresenter {

  constructor(configModel) {
    this._model = configModel;
    this._container = this._model._container;
    this._configView = new ConfigView(this._model._data, this._container);
  }

  onInit() {
    this._configView.setProxyStatus();
    this._configView.setConfig();
    this._configView.togglePushServerField();
    this._configView.clearLoader(this._container);
    this._configView.bindHandlers();
  }
}
