import jquery from 'jquery';
window.jQuery = jquery;
import 'bootstrap';
import App from './App';
import {status} from './utils';
import 'promise-polyfill/src/polyfill';
import 'whatwg-fetch';
import moment from 'moment/src/moment';
window.moment = moment;

document.addEventListener('DOMContentLoaded', () => {
  App.container = $('.container');
  App.showNavbar();

  window.fetch('/config/get')
    .then(status)
    .then((response) => response.json())
    .then((data) => {
      App.config = data;
      getAccounts();
    })
    .then(App.showConfig)
    .catch(App.showConfigError);

  const getAccounts = () => {
    window.fetch('/account/list')
      .then(status)
      .then((response) => response.json())
      .then((data) => {
        App.accounts = data;
      })
      .then(App.showAccounts)
      .catch(App.showAccountsError);
  };

  window.fetch('/info/build')
    .then(status)
    .then((response) => response.json())
    .then((data) => {
      App.info = data;
    })
    .then(App.showInfo)
    .catch(() => {
      console.warn('   ')
    });

  console.log('Initialized app');
});
