import ParentClass from './ParentClass';
import AccountView from './AccountView';
import FolderView from './FolderView';
import App from '../App';
import {Alert, AccountsMessages, LoadMessages} from './type-data';
import {status, fetchHeaders} from "../utils";

export default class AccountsView extends ParentClass {
    constructor(accountsData, accountsContainer, configData) {
        super();
        this._data = accountsData;
        this._container = accountsContainer;
        this._config = configData;
        this._accountsTable = $('.jAccountsTable', this._container);
        this._accountsList = $('tbody', this._accountsTable);
        this._btnAdd = $('.jAccountAddButton', this._container);
        this._fieldImport = $('.jAccountImportInput', this._container);
        this._modal = $('#accountModal');
        this._confirm = $('#confirmModal');
        this._btnSave = $('.jAccountSubmitButton', this._modal);
        this._btnBrowse = $('.jFolderBrowseButton', this._modal);
        this._btnDelete = $('.jAccountDeleteButton', this._confirm);
        this._modalForm = $('form', this._modal)[0];
        this._selectedAccount = null;
        this._foldersModal = $('#folderModal');
        this._foldersModalContent = $('.modal-body', this._foldersModal);
        this._fieldPath = $('.jFolderPathField', this._foldersModal);
        this._btnUp = $('.jFolderUpButton', this._foldersModal);
        this._btnApply = $('.jFolderApplyButton', this._foldersModal);
        this._btnRoot = $('.jFolderRootButton', this._foldersModal);
        this._btnHome = $('.jFolderHomeButton', this._foldersModal);
        this._btnProgram = $('.jFolderProgramButton', this._foldersModal);
        this._btnHistory = $('.jConfigHistoryButton');
        this._selectedFolderID = '';
        this._selectedFolderPath = '';
        this._activeFolderModalPath = '';
        this._timeout = 15000;
        this._timeoutHandler = null;
    }

    addAccountsList() {
        this._data.forEach((account) => {
            let accountView = new AccountView(account, this._container, this._modal);
            this._accountsList.append(accountView.element);
            accountView.onDeleteAccount = this.onDeleteAccount.bind(this);
        });
    }

    addFoldersList(folders) {
        let foldersList = folders[0].root ? folders[0].child : folders;
        foldersList.forEach((folder) => {
            let folderView = new FolderView(folder, this._foldersModal);
            $('tbody', this._foldersModal).append(folderView.element);
            folderView.onOpenFolder = this.onOpenFolder.bind(this);
            folderView.onSelectFolder = this.onSelectFolder.bind(this);
        });
    }

    setMessage(messageText, type) {
        this.removeMessage(this._container);
        this.addMessage(this._container, messageText, type);
    }

    hideModal() {
        this._modal.modal('hide');
    }

    hideConfirm() {
        this._confirm.modal('hide');
    }

    setFolderFieldPath() {
        this._fieldPath.val(this._activeFolderModalPath)
    }

    trimFolderPath(path) {
        return path.replace(/((?!^)([\/\\][^(\/|\\)]+$))|(([^(\/|\\)]+)(?!.*[\/\\]))/g, '');
    }

    getFormData() {
        let formData = new FormData(this._modalForm);
        let postData = {};

        for (let [key, value] of formData.entries()) {
            postData[key] = value;
        }
        return postData;
    }

    setTimeoutHandler() {
        this._timeoutHandler = setTimeout(this.getAccountsList.bind(this), this._timeout);
    }

    getAccountsList(userTrigger) {
        this.setLoader(this._container);
        window.fetch('/account/list', {headers: fetchHeaders()})
            .then(status)
            .then((response) => response.json())
            .then((data) => {
                this._data = data;
                App.accounts = data;
                this.clearAccountsList();
                this.addAccountsList();
                this.setIntervalField();
                this.setHistoryButtonStatus();
                this.clearLoader(this._container);
            })
            .then(() => {
                if (!userTrigger) {
                    this.setTimeoutHandler();
                }
            })
            .catch(() => {
                this.clearLoader(this._container);
                this.setMessage(LoadMessages.LOAD_FAIL, Alert.DANGER);
            });
    }

    getFoldersList(url) {
        this.setLoader(this._foldersModal);
        let path = !!url ? url : '/folders?path=' + encodeURIComponent(this._activeFolderModalPath);
        window.fetch(path, {headers: fetchHeaders()})
            .then(status)
            .then((response) => response.json())
            .then((data) => {
                this.clearFoldersList();

                if (data.length > 0) {
                    this.addFoldersList(data);

                    if (data[0].root) {
                        this._activeFolderModalPath = data[0].pathName;
                    }

                    if (url) {
                        this._activeFolderModalPath = this.trimFolderPath(data[0].pathName)
                    }
                }
                this.clearSelectedFolderPath();

                if (this._activeFolderModalPath) {
                    this.setFolderFieldPath();
                }
                this.clearLoader(this._foldersModal);
            })
            .catch(() => {
                this.clearLoader(this._foldersModal);
                this.removeMessage(this._foldersModalContent);
                this.addMessage(this._foldersModalContent, LoadMessages.LOAD_FAIL, Alert.DANGER);
            });
    }

    onOpenFolder(selectedFolder) {
        if (selectedFolder) {
            this._activeFolderModalPath = selectedFolder.pathName;
            this.clearFoldersList();
            this.getFoldersList();
        }
    }

    onSelectFolder(selectedFolder) {
        this._selectedFolderPath = selectedFolder.pathName;
    }

    clearAccountsList() {
        this._accountsList.html('');
    }

    clearFoldersList() {
        $('tbody', this._foldersModal).html('');
    }

    clearSelectedFolderPath() {
        this._selectedFolderPath = '';
    }

    postAccount() {
        this.setLoader(this._modal);
        let data = this.getFormData();

        window.fetch('/account/save', {
            method: 'POST',
            body: JSON.stringify(data),
            headers: {
                'Content-Type': 'application/json'
            }
        })
            .then(status)
            .then(() => {
                this.clearLoader(this._modal);
                this.hideModal();
                this.getAccountsList(true);
                this.setMessage(AccountsMessages.SAVE_SUCCESS, Alert.SUCCESS);
            })
            .catch(() => {
                this.clearLoader(this._modal);
                this.hideModal();
                this.getAccountsList(true);
                this.setMessage(AccountsMessages.SAVE_FAIL, Alert.DANGER);
            });
    }

    postFileAccount() {
        this.setLoader(this._container);

        let data = new FormData();
        data.append('file', this._fieldImport[0].files[0]);

        window.fetch('/account/file/save', {
            method: 'POST',
            body: data
        })
            .then(status)
            .then(() => {
                this.getAccountsList(true);
                this.setMessage(AccountsMessages.IMPORT_SUCCESS, Alert.SUCCESS);
            })
            .catch(() => {
                this.getAccountsList(true);
                this.setMessage(AccountsMessages.IMPORT_FAIL, Alert.DANGER);
            });
    }

    onDeleteAccount(selectedAccount) {
        if (selectedAccount) {
            this._selectedAccount = selectedAccount;

            this._confirm.find('.jConfirmText')
                .html(`     <span class="font-weight-bold font-italic">${selectedAccount.login}</span>?`);
        }
    }

    deleteAccount() {
        this.setLoader(this._container);
        fetch('/account/delete/' + this._selectedAccount.id, {
            method: 'delete'
        }).then(status)
            .then(() => {
                this.getAccountsList(true);
                this.setMessage(AccountsMessages.DELETE_SUCCESS, Alert.SUCCESS);
                this.clearLoader(this._container);
            })
            .catch(() => {
                this.clearLoader(this._modal);
                this.getAccountsList(true);
                this.setMessage(AccountsMessages.DELETE_FAIL, Alert.DANGER);
            });
    }

    setIntervalField() {
        if (App.config.serverMode === "TIMER") {
            $('#accountRequestInterval', this._modal)
                .closest('.form-group')
                .removeClass('d-none');

            $('.toggleInterval').addClass('d-lg-table-cell');

            if ((/^\#\#\#\w+/).test(App.config.serverUsername)) {
                $('select[name="requestInterval"]', this._modal)
                    .attr('id', '_accountRequestInterval')
                    .prop('disabled', true)
                    .addClass('d-none');
                $('input[name="requestInterval"]', this._modal)
                    .attr('id', 'accountRequestInterval')
                    .prop('disabled', false)
                    .removeClass('d-none');
            } else {
                $('input[name="requestInterval"]', this._modal)
                    .attr('id', '_accountRequestInterval')
                    .prop('disabled', true)
                    .addClass('d-none');
                $('select[name="requestInterval"]', this._modal)
                    .attr('id', 'accountRequestInterval')
                    .prop('disabled', false)
                    .removeClass('d-none');
            }
        } else if (App.config.serverMode === "PUSH") {
            $('#accountRequestInterval', this._modal)
                .prop('disabled', true)
                .closest('.form-group')
                .addClass('d-none');

            $('.toggleInterval').removeClass('d-lg-table-cell');
        }
    }

    setHistoryButtonStatus() {
        if(this._data.length > 0) {
            this._btnHistory
                .prop('disabled', true)
                .addClass('d-none')
        } else {
            this._btnHistory
                .prop('disabled', false)
                .removeClass('d-none')
        }
    }

    bindHandlers() {
        this._btnSave
            .on('click', (evt) => {
                evt.preventDefault();
                this.postAccount();
            });

        this._btnDelete
            .on('click', (evt) => {
                evt.preventDefault();
                this.deleteAccount();
                this.hideConfirm();
            });

        this._foldersModal
            .on('show.bs.modal', (evt) => {
                let currentButton = $(evt.relatedTarget);
                this._selectedFolderID = currentButton
                    .closest('.input-group')
                    .find('.jAccountDir')
                    .attr('id');
                let browseField = $('#' + this._selectedFolderID).val();
                if (browseField) {
                    this._activeFolderModalPath = browseField;
                    this.getFoldersList();
                } else {
                    this.getFoldersList('folders/home');
                }
                $('#folderModalLabel', this._foldersModal).text(currentButton.data('title'))
            })
            .on('hidden.bs.modal', () => {
                this._selectedFolderID = '';
                this.clearSelectedFolderPath();
                this._activeFolderModalPath = '';
            });

        this._modal
            .on('show.bs.modal', (evt) => {
                let button = $(evt.relatedTarget); // Button that triggered the modal
                let recipient = button.data('title'); // Extract info from data-* attributes
                $(this).find('#accountModalLabel').text(recipient + ' ');
            })
            .on('hidden.bs.modal', () => {
                $('form', this._modal)[0].reset();
                this.clearValidation(this._modal);
            });

        this._btnUp
            .on('click', () => {
                this._activeFolderModalPath = this.trimFolderPath(this._activeFolderModalPath);
                this.getFoldersList();
            });

        this._btnHome
            .on('click', () => {
                this.getFoldersList('folders/home');
            });

        this._btnProgram
            .on('click', () => {
                this.getFoldersList('folders/program');
            });

        this._btnRoot
            .on('click', () => {
                this.getFoldersList('folders');
            });

        this._btnApply
            .on('click', () => {
                $('#' + this._selectedFolderID).val(this._selectedFolderPath || this._activeFolderModalPath);
                this._foldersModal.modal('hide');
            });

        this.onUserInputClearValidation(this._modal);

        this._fieldImport
            .on('change', () => {
                if (this._fieldImport.val() !== '') {
                    this.postFileAccount();
                }
            })
    }

}
