import ParentClass from './ParentClass';
import {SelectionClass} from './type-data';

export default class HistoryItemView extends ParentClass {
    constructor(historyItem, modal) {
        super();
        this._modal = modal;
        this._historyItem = historyItem;
    }

    /*set onOpenFolder(handler) {
        this._onOpenFolder = handler;
    }*/

    set onSelectHistoryItem(handler) {
        this._onSelectHistoryItem = handler;
    }

    getMarkup() {
        return `<th scope="row" style="width:30px;" class="history-item jHistoryConfigViewBtn">
                <span href="" class="jHistoryConfigViewBtn">
                  <i class="fas fa-cogs text-secondary"></i>
                </span>
              </th>
              <td class="history-item jHistoryConfigViewBtn">                
                 <span class="text-body jHistoryPath">${this._historyItem}</span>
              </td>`;
    }

    bindHandlers() {

        $('.jHistoryConfigViewBtn', this.element)
            .on('click', () => {
                this._modal
                    .find('.' + SelectionClass.ROW_ACTIVE)
                    .removeClass(SelectionClass.ROW_ACTIVE);

                $(this.element).addClass(SelectionClass.ROW_ACTIVE);

                this._onSelectHistoryItem(this._historyItem);
            });

    }

}
