import ParentClass from './ParentClass';

export default class InfoView extends ParentClass {
    constructor(infoContainer, infoData) {
        super();
        this._container = infoContainer;
        this._data = infoData;
        this._title = document.title;
        this._version = $('.jClientVersion', this._container)
        this._build = $('.jClientBuild', this._container)
    }

    onInit() {
        this.bindHandlers();
    }

    bindHandlers() {
        this._version.text(this._data.version);
        this._build.text(this._data.buildDate);
        $('.invisible', this._container).removeClass('invisible');

        document.title = `${this._title} ${this._data.version}`;
    }
}
