import ParentClass from './ParentClass';
import App from '../App';
import {status, fetchHeaders} from '../utils';
import {ConfigCode, Alert, ConfigMessages, LoadMessages} from './type-data';
import HistoryItemView from "./HistoryItemView";

export default class ConfigView extends ParentClass {
    constructor(configData, configContainer) {
        super();
        this._data = configData;
        this._container = configContainer;
        this._fieldsCommon = $('.jConfigCommon', this._container);
        this._statusProxy = $('.jStatusProxy input[name=proxy_active]', this._container);
        this._proxy = $('.jConfigProxy', this._container);
        this._proxyFields = $('input, select', this._proxy);
        this._btnSubmit = $('.jConfigSubmitButton', this._container);
        this._btnExport = $('.jConfigExportButton', this._container);
        this._fieldImport = $('.jConfigImportInput', this._container);
        this._btnHistory = $('.jConfigHistoryButton', this._container);
        this._accountModal = $('#accountModal');
        this._historyModal = $('#historyModal');
        this._historyList = $('.jHistory', this._historyModal);
        this._historyEmpty = $('.jHistoryEmpty', this._historyModal);
        this._btnHistoryApply = $('.jHistoryApplyButton', this._historyModal);
        this._historyItemConfigContainer = $('.jHistoryItemConfig', this._historyModal);
        this._logDaysDefault = 7;
        this._activeHistoryPath = '';
        this.fieldName = new Map();
        this.fieldName.set(ConfigCode.INCORRECT_URL, 'url');
        this.fieldName.set(ConfigCode.INCORRECT_LOGIN, 'serverUsername');
        this.fieldName.set(ConfigCode.INVALID_PUSH_SERVER_URL, 'pushServerUrl');
        this.fieldName.set(ConfigCode.INCORRECT_PROXY_URL, 'proxy_url');
        this.fieldName.set(ConfigCode.INCORRECT_PROXY_PORT, 'proxy_port');
    }

    getFormData() {
        let formData = new FormData(this._container[0]);
        let postData = {proxy: {}};

        for (let [key, value] of formData.entries()) {
            if (key.match(/^proxy_/)) {
                postData.proxy[key.replace(/^proxy_/, '')] = value;
            } else if (key !== 'importFile') {
                postData[key] = value;
            }
        }

        return postData;
    }

    setConfig() {
        for (let option in this._data) {
            let value = this._data[option];
            if (option === 'operationLogDays' && value === null) {
                value = this._logDaysDefault;
            }
            this._fieldsCommon.find('.form-control[name=' + option + ']').val(value);
        }

        for (let option in this._data.proxy) {
            this._proxy.find('.form-control[name=proxy_' + option + ']').val(this._data.proxy[option]);
        }

        if (this._data.validationError > 0) {
            this._container.find('.form-control[name=' + this.fieldName.get(this._data.validationError) + ']')
                .addClass('is-invalid');
        }
    }

    setMessage(messageText, type) {
        this.removeMessage(this._container);
        this.addMessage(this._container, messageText, type);
    }

    updateConfig() {
        window.fetch('/config/get', {headers: fetchHeaders()})
            .then(status)
            .then((response) => response.json())
            .then((data) => {
                this._data = data;
                this.clearValidation(this._container);
                this.setConfig();
                this.clearLoader(this._container);
                this.togglePushServerField();
                App.refreshAccounts();
                App.config = data;
            })
            .catch(() => {
                this.clearLoader(this._container);
                this.setMessage(LoadMessages.LOAD_FAIL, Alert.DANGER);
            });
    }

    setProxyStatus() {
        this._statusProxy.prop('checked', this._data.proxy.active);
        this.toggleProxyStatus();
    }

    toggleProxyStatus() {
        if (!!this._statusProxy.prop('checked')) {
            this._proxyFields.each((i, field) => {
                $(field).prop('disabled', false);
            })
        } else {
            this._proxyFields.each((i, field) => {
                $(field).prop('disabled', true);
            })
        }
    }

    getConfigFile() {
        let fileName;
        this.setLoader(this._container);
        window.fetch('/config/file/save')
            .then(status)
            .then((response) => {
                fileName = this.getFileName(response.headers.get('content-disposition'));
                return response.blob()
            })
            .then((blob) => {
                this.downloadFile(blob, fileName);
                this.clearLoader(this._container);
                this.setMessage(ConfigMessages.EXPORT_SUCCESS, Alert.SUCCESS);
            })
            .catch(() => {
                this.clearLoader(this._container);
                this.setMessage(ConfigMessages.EXPORT_FAIL, Alert.DANGER);
            });
    }

    postConfig() {
        this.setLoader(this._container);
        let data = this.getFormData();

        window.fetch('/config/save', {
            method: 'POST',
            body: JSON.stringify(data),
            headers: {
                'Content-Type': 'application/json'
            }
        })
            .then(status)
            .then(() => {
                this.updateConfig();
                this.clearLoader(this._container);
                this.setMessage(ConfigMessages.SAVE_SUCCESS, Alert.SUCCESS);
            })
            .catch(() => {
                this.updateConfig();
                this.clearLoader(this._container);
                this.setMessage(ConfigMessages.SAVE_FAIL, Alert.DANGER);
            });
    }

    postFileConfig() {
        this.setLoader(this._container);

        let data = new FormData();
        data.append('file', this._fieldImport[0].files[0]);

        window.fetch('/config/file/load', {
            method: 'POST',
            body: data
        })
            .then(status)
            .then(() => {
                this.updateConfig();
                this.setMessage(ConfigMessages.IMPORT_SUCCESS, Alert.SUCCESS);
            })
            .catch(() => {
                this.updateConfig();
                this.setMessage(ConfigMessages.IMPORT_FAIL, Alert.DANGER);
            });
    }

    togglePushServerField() {
        let mode = $('#serverMode', this._container).val();

        if (mode === 'PUSH') {
            $('#pushServerUrl', this._container)
                .prop('disabled', false)
                .closest('.form-group')
                .removeClass('d-none');
        } else if (mode === 'TIMER') {
            $('#pushServerUrl', this._container)
                .prop('disabled', true)
                .closest('.form-group')
                .addClass('d-none');
        }
    }

    getHistory() {
        this.setLoader(this._historyModal);

        window.fetch('/config/old/list', {headers: fetchHeaders()})
            .then(status)
            .then((response) => response.json())
            .then((data) => {
                this.clearLoader(this._historyModal);
                this.addHistoryList(data);
            })
            .catch(() => {
                this.clearLoader(this._historyModal);
                this.addMessage(this._historyModal, LoadMessages.LOAD_FAIL, Alert.DANGER);
            });
    }

    addHistoryList(history) {
        history.forEach((configPath) => {
            let historyItemView = new HistoryItemView(configPath, this._historyModal);
            this._historyList.append(historyItemView.element);
            historyItemView.onSelectHistoryItem = this.onSelectHistoryItem.bind(this);
        });

        if(history.length === 0) {
            this._historyEmpty.removeClass('d-none');
        }
    }

    onSelectHistoryItem(selectedHistoryPath) {
        if (selectedHistoryPath) {
            this._activeHistoryPath = encodeURIComponent(selectedHistoryPath);
            this.getHistoryItemConfig();
            this._btnHistoryApply.prop('disabled', false);
        }
    }

    getHistoryItemConfig() {
        this.setLoader(this._historyModal);

        window.fetch(`/config/old?path=${this._activeHistoryPath}`, {headers: fetchHeaders()})
            .then(status)
            .then((response) => response.json())
            .then((data) => {
                this.clearLoader(this._historyModal);
                this.showHistoryItemConfig(data);
            })
            .catch(() => {
                this.clearLoader(this._historyModal);
                this.addMessage(this._historyModal, LoadMessages.LOAD_FAIL, Alert.DANGER);
            });
    }

    showHistoryItemConfig(config) {
        this._historyItemConfigContainer.text(JSON.stringify(config, undefined, 2))
    }

    clearHistoryList() {
        this._historyList.html('');
    }

    clearHistoryItemConfig() {
        this._historyItemConfigContainer.html('');
    }

    restoreConfig() {
        this.setLoader(this._container);

        window.fetch(`/config/old/load?path=${this._activeHistoryPath}`, {headers: fetchHeaders()})
            .then(status)
            .then(() => {
                this.updateConfig();
                this.setMessage(ConfigMessages.SAVE_SUCCESS, Alert.SUCCESS);
            })
            .catch(() => {
                this.clearLoader(this._container);
                this.setMessage(ConfigMessages.SAVE_FAIL, Alert.DANGER);
            });
    }

    bindHandlers() {
        this.onUserInputClearValidation(this._container);

        this._statusProxy
            .on('click', () => {
                this.toggleProxyStatus()
            });

        this._btnSubmit
            .on('click', (evt) => {
                evt.preventDefault();
                this.postConfig();
            });

        this._btnExport
            .on('click', (evt) => {
                evt.preventDefault();
                this.getConfigFile();
            });

        this._fieldImport
            .on('change', () => {
                if (this._fieldImport.val() !== '') {
                    this.postFileConfig();
                }
            });

        this._btnHistory
            .on('click', (evt) => {
                evt.preventDefault();
                this.getHistory();
            });

        $('#serverMode')
            .on('change', () => {
                this.togglePushServerField();
            });

        this._historyModal
            .on('hidden.bs.modal', () => {
                this.clearHistoryItemConfig();
                this.clearHistoryList();
                this._btnHistoryApply.prop('disabled', true);
                this._activeHistoryPath = '';
                this._historyEmpty.addClass('d-none');
            });

        this._btnHistoryApply
            .on('click', (evt) => {
                evt.preventDefault();
                this.restoreConfig();
                this._historyModal.modal('hide');
            });
    }
}
