import ParentClass from './ParentClass';
import {Alert, LoadMessages} from './type-data';

export default class ErrorView extends ParentClass {
    constructor(container) {
        super();
        this._container = container;
    }

    onInit() {
        this.clearLoader(this._container);
        this.addMessage(this._container, LoadMessages.LOAD_FAIL, Alert.DANGER);
    }
}
