import ParentClass from './ParentClass';
import {
    Alert,
    AccountIcon,
    AccountValidationCode,
    AccountDuplicateCode,
    AccountIconTitle,
    TableClass,
    Prefixes
} from './type-data';

export default class AccountView extends ParentClass {
    constructor(account, container, modal) {
        super();
        this._modal = modal;
        this._container = container;
        this._account = account;
        this.cellValidationClassName = new Map();
        this.cellValidationClassName.set(AccountValidationCode.EMPTY_LOGIN, 'login');
        this.cellValidationClassName.set(AccountValidationCode.EMPTY_PASSWORD, 'password');
        this.cellValidationClassName.set(AccountValidationCode.EMPTY_INTERVAL, 'requestInterval');
        this.cellValidationClassName.set(AccountValidationCode.INCORRECT_SEND_DIR, 'sendDir');
        this.cellValidationClassName.set(AccountValidationCode.INCORRECT_RECEIVE_DIR, 'receiveDir');
        this.cellValidationClassName.set(AccountValidationCode.EMPTY_SERVER_ID, 'serverId');
        this.cellDuplicateClassName = new Map();
        this.cellDuplicateClassName.set(AccountDuplicateCode.DUPLICATE_SEND_DIR, 'sendDir');
        this.cellDuplicateClassName.set(AccountDuplicateCode.DUPLICATE_RECEIVE_DIR, 'receiveDir');
        this.cellDuplicateClassName.set(AccountDuplicateCode.DUPLICATE_LOGIN, 'login');
    }

    set onDeleteAccount(handler) {
        this._onDeleteAccount = handler;
    }

    hasErrors() {
        return this._account.validationError > 0 || this._account.transportError > 0 || this._account.duplicateError > 0
    }

    getIconsListTemplate() {
        let iconsTemplate = '';

        const getIconTemplate = (iconClass, iconColor, iconTitle) => {
            return `<i class="fas fa-${iconClass} ${iconColor} mr-1" title="${iconTitle}"></i>`;
        };

        if (this.hasErrors()) {
            if (this._account.validationError > 0) {
                iconsTemplate += getIconTemplate(AccountIcon.VALIDATION_ERROR, Prefixes.TEXT + Alert.DANGER, AccountIconTitle.VALIDATION_ERROR)
            }
            if (this._account.transportError > 0) {
                iconsTemplate += getIconTemplate(AccountIcon.TRANSPORT_ERROR, Prefixes.TEXT + Alert.DANGER, AccountIconTitle.TRANSPORT_ERROR)
            }
            if (this._account.duplicateError > 0) {
                iconsTemplate += getIconTemplate(AccountIcon.DUPLICATE_ERROR, Prefixes.TEXT + Alert.DANGER, AccountIconTitle.DUPLICATE_ERROR)
            }
        } else {
            iconsTemplate += getIconTemplate(AccountIcon.OK, Prefixes.TEXT + Alert.SUCCESS, AccountIconTitle.OK)
        }

        return iconsTemplate;
    }

    getMarkup() {
        return `<th scope="row" class="text-nowrap">${this.getIconsListTemplate()}</i></th>
            <td class="${Prefixes.ACCOUNT}login">${this._account.login}</td>
            <td class="${Prefixes.ACCOUNT}password d-none d-sm-table-cell">${this._account.password}</td>
            <td class="${Prefixes.ACCOUNT}serverId d-none d-sm-table-cell">${this._account.serverId}</td>                    
            <td class="${Prefixes.ACCOUNT}sendDir d-none d-lg-table-cell">${this._account.sendDir}</td>
            <td class="${Prefixes.ACCOUNT}receiveDir d-none d-lg-table-cell">${this._account.receiveDir}</td>
            <td class="${Prefixes.ACCOUNT}requestInterval d-none d-lg-table-cell toggleInterval">${this._account.requestInterval}</td>
            <td class="${Prefixes.ACCOUNT}msgType d-none d-lg-table-cell">${this._account.msgType}</td>
            <td class="text-nowrap">                    
              <a href="#" class="jAccountEditButton" 
                 data-title=" "
                 data-toggle="modal"
                 data-target="#accountModal"><i class="fas fa-edit mr-1"></i></a> 
              <a href="#" class="text-danger jAccountConfirmDeleteButton"
                 data-toggle="modal"
                 data-target="#confirmModal"><i class="fas fa-times-circle"></i></a>
            </td>`;
    }

    setModalForm() {
        for (let option in this._account) {
            this._modal.find('.form-control[name=' + option + ']')
                .val(this._account[option]);
        }

        if (this._account.validationError > 0) {
            this._modal.find('.form-control[name=' + this.cellValidationClassName.get(this._account.validationError) + ']')
                .addClass('is-invalid validation-error');
        }

        if (this._account.duplicateError > 0) {
            this._modal.find('.form-control[name=' + this.cellDuplicateClassName.get(this._account.duplicateError) + ']')
                .addClass('is-invalid duplication-error');
        }
    }

    bindHandlers() {
        if (this._account.validationError > 0) {
            $('.' + Prefixes.ACCOUNT + this.cellValidationClassName.get(this._account.validationError), this.element)
                .addClass(Prefixes.TEXT + Alert.DANGER);
        }

        if (this._account.duplicateError > 0) {
            $('.' + Prefixes.ACCOUNT + this.cellDuplicateClassName.get(this._account.duplicateError), this.element)
                .addClass(Prefixes.TEXT + Alert.DANGER);
        }

        if (this.hasErrors()) {
            $(this.element).addClass(TableClass.DANGER);
        }

        $('.jAccountEditButton', this.element)
            .on('click', () => {
                this.setModalForm();
            });

        $('.jAccountConfirmDeleteButton', this.element)
            .on('click', () => {
                this._onDeleteAccount(this._account);
            });
    }

}
