import AccountsView from './classes/AccountsView';

export default class AccountsPresenter {

  constructor(accountsModel) {
    this._model = accountsModel;
    this._container = this._model._container;
    this._accountsView = new AccountsView(this._model._data, this._container, this._model._config);
  }

  onInit() {
    this._accountsView.addAccountsList();
    this._accountsView.clearLoader(this._container);
    this._accountsView.bindHandlers();
    this._accountsView.setIntervalField();
    this._accountsView.setTimeoutHandler();
    this._accountsView.setHistoryButtonStatus();
  }

  onUpdateConfig() {
    this._accountsView.getAccountsList(true);
  }
}
