export default class ParentClass {

    get element() {
        if (!this._element) {
            this._element = document.createElement('tr');
            this._element.innerHTML = this.getMarkup();
            this.bindHandlers();
        }
        return this._element;
    }

    getMarkup() {
        throw new Error('Abstract method should be implemented');
    }

    bindHandlers() {
    }

    clearHandlers() {
    }


    setLoader(element) {
        if (element.hasClass('modal')) {
            $('.modal-dialog', element).addClass('loader');
        } else {
            element.addClass('loader');
        }
    }

    clearLoader(element) {
        if (element.hasClass('modal')) {
            $('.modal-dialog', element).removeClass('loader');
        } else {
            element.removeClass('loader');
        }
    }

    clearValidation(container) {
        $('.form-control', container).removeClass('is-invalid');
    }

    onUserInputClearValidation(container) {
        container
            .on('keyup', '.form-control', (evt) => {
                $(evt.currentTarget).removeClass('is-invalid');
            });
    }

    downloadFile(blob, filename) {
        let url = window.URL.createObjectURL(blob);
        let link = document.createElement('a');
        link.href = url;
        link.download = filename;
        document.body.appendChild(link); // we need to append the element to the dom -> otherwise it will not work in firefox
        link.click();
        link.remove();
    }

    getFileName(disposition) {
        let filename;
        if (disposition && disposition.indexOf('attachment') !== -1) {
            let filenameRegex = /filename[^;=\n]*=((['"]).*?\2|[^;\n]*)/;
            let matches = filenameRegex.exec(disposition);
            if (matches != null && matches[1]) {
                filename = matches[1].replace(/['"]/g, '');
            }
        }
        return filename;
    }

    addMessage(container, text, status) {
        if (!status) {
            status = 'primary';
        }
        const template = `<div class="alert alert-${status} fade show mt-4 mb-0" role="alert">
        ${text}
        <button type="button" class="close" data-dismiss="alert" aria-label="Close">
          <span aria-hidden="true">&times;</span>
        </button>
      </div>`;

        container.append(template);

        setTimeout(() => {
            container.find('.alert-' + status).alert('close');
        }, 10000)
    }

    getModalFormData(form) {
        let formData = new FormData(form);
        let accountData = {};

        for (let [key, value] of formData.entries()) {
            accountData[key] = value;
        }

        return accountData;
    }

    removeMessage(container) {
        container.find('.alert').remove();
    }
}
