import ParentClass from './ParentClass';
import {SelectionClass} from './type-data';

export default class FolderView extends ParentClass {
    constructor(folder, modal) {
        super();
        this._modal = modal;
        this._folder = folder;
    }

    set onOpenFolder(handler) {
        this._onOpenFolder = handler;
    }

    set onSelectFolder(handler) {
        this._onSelectFolder = handler;
    }

    trimFolderCurrentPath() {
        return this._folder.pathName.replace(/(\/[^\/]+$|\/+$)/g, '');
    }

    trimFolderName() {
        let name = this._folder.pathName.match(/(\/[^\/]+$|\/+$)/g);
        if (name) {
            name = name[name.length - 1]
        } else {
            name = ''
        }
        return name;
    }

    getMarkup() {
        return `<th scope="row" style="width:30px;">
                <a href="" class="jFolderOpenButton">
                  <i class="fas fa-folder text-secondary"></i>
                </a>
              </th>
              <td>
                <a href="" class="jFolderOpenButton text-black-50">
                  ${this.trimFolderCurrentPath()}<span class="text-body">${this.trimFolderName()}</span></a>
              </td>
              <td class="text-nowrap text-right">                      
                <a href="" class="jFolderSelectButton text-secondary" title=" ">
                  <i class="far fa-circle mr-1"></i></a> 
              </td>`;
    }

    bindHandlers() {
        let selectBtn = $('.jFolderSelectButton', this.element);

        $('.jFolderOpenButton', this.element)
            .on('click', (evt) => {
                evt.preventDefault();
                this._onOpenFolder(this._folder);
            });

        selectBtn
            .on('click', (evt) => {
                evt.preventDefault();
                this._modal
                    .find('.' + SelectionClass.ROW_ACTIVE)
                    .removeClass(SelectionClass.ROW_ACTIVE)
                    .find('i')
                    .removeClass(SelectionClass.RADIO_ACTIVE)
                    .addClass(SelectionClass.RADIO);

                selectBtn
                    .find('i')
                    .removeClass(SelectionClass.RADIO)
                    .addClass(SelectionClass.RADIO_ACTIVE)
                    .closest('tr')
                    .addClass(SelectionClass.ROW_ACTIVE);
                this._onSelectFolder(this._folder);
            });
    }

}
