import ParentClass from './ParentClass';
import App from '../App';

export default class NavbarView extends ParentClass {
    constructor(navContainer) {
        super();
        this._container = navContainer;
        this._data = {};
    }

    onInit() {
        this.bindHandlers();
    }

    bindHandlers() {
        this._container
            .on('shown.bs.tab', 'a[data-toggle="tab"]', function (evt) {
                let operationType = $(evt.target).data('type');
                App.setOperationsType = operationType;

                if (operationType === 'receive') {
                    App.showReceive();
                    App.hideSend();
                } else if (operationType === 'send') {
                    App.showSend();
                    App.hideReceive();
                } else {
                    App.hideReceive();
                    App.hideSend();
                }
            })
            .on('dblclick', '.fa-cat', (evt) => {
                $(evt.currentTarget).removeClass('transparent');
            })
    }
}
