import ParentClass from './ParentClass';
import OperationView from "./OperationView";
import {status, fetchHeaders} from "../utils";
import App from "../App";
import {Alert, PaginationCaptions, AccountsMessages} from './type-data';

export default class OperationsView extends ParentClass {
    constructor(type, container, accounts) {
        super();
        this._container = container;
        this._modal = $('#exceptionMessageModal');
        this._timeout = 15000;
        this._type = type;
        this._accounts = accounts;
        this._data = {};
        this._filter = $('.jOperationsFilter', this._container);
        this._pageOptions = $('.jPageOptionsContainer', this._container);
        this._pageSizeSelect = $('.jPageSize', this._pageOptions);
        this._filterLogin = $('select[name="filterLogin"]', this._filter);
        this._operationsTable = $('.jOperationsTable', this._container);
        this._operationsList = $('tbody', this._operationsTable);
        this._pagination = $('.jPaginationContainer', this._container);
        this._selectedPage = 0;
        this._timeoutHandler = null;
        this._pageSize = this._pageSizeSelect.val();
        this._paginationOffset = 6;
        this._typeParametr = {
            receive: 'RECEIVE',
            send: 'SEND'
        }
    }

    onShow() {
        this.addLoginList();

        if (this._accounts.length > 0) {
            this.getOperationsList();
        } else {
            this.clearOperationsList();
            this.addMessage(this._filter.parent(), AccountsMessages.EMPTY_LIST, Alert.WARNING)
        }

        this.bindHandlers();
    }

    clearOperationsList() {
        this._operationsList.html('');
    }

    setTimeoutHandler() {
        this._timeoutHandler = setTimeout(this.getOperationsList.bind(this), this._timeout);
    }

    getOperationsList(userTrigger) {
        this.setLoader(this._container);
        let parameters = `logType=${this._typeParametr[this._type]}&size=${this._pageSize}&login=`;

        if (!!this._filterLogin.val()) {
            parameters += this._filterLogin.val();
        } else if (this._accounts.length > 0) {
            parameters += this._accounts[0].login;
        }
        if (!!this._selectedPage) {
            parameters += `&page=${this._selectedPage}`
        }
        window.fetch(`/operation/list?${parameters}`, {headers: fetchHeaders()})
            .then(status)
            .then((response) => response.json())
            .then((data) => {
                this._data = data;
                this.clearOperationsList();
                this.clearPagination();
                this.addOperationsList();

                this.clearLoader(this._container);
            })
            .then(() => {
                if (!userTrigger) {
                    this.setTimeoutHandler();
                }
                this.getPagination();
            })
            .catch(App.showError);
    }

    addOperationsList() {
        this._data.content.forEach((operation) => {
            const operationView = new OperationView(operation, this._modal);
            this._operationsList.append($(operationView.element));
        });
    }

    addLoginList() {
        let template = ``;
        if (this._accounts) {
            this._accounts.forEach((account, i) => {
                template += `<option value="${account.login}" ${i === 0 ? 'selected' : ''}>${account.login}</option>`
            });
        }
        this._filterLogin.html(template);
    }

    getPagination() {
        let template = ``;
        let currentNumber = this._selectedPage;

        if (this._selectedPage > 1) {
            template += `<li class="page-item">
          <a class="page-link" href="#" aria-label="First">
            <span aria-hidden="true">&laquo; ${PaginationCaptions.FIRST}</span>
            <span class="sr-only">${PaginationCaptions.FIRST}</span>
          </a>
        </li>`
        }

        if (this._selectedPage !== 0) {
            template += `<li class="page-item">
          <a class="page-link" href="#" aria-label="Previous">
            <span aria-hidden="true">&laquo;</span>
            <span class="sr-only">${PaginationCaptions.PREV}</span>
          </a>
        </li>`
        }

        template += `<li class="page-item active"><span class="page-link">${currentNumber + 1}</span></li>`;

        if (!this._data.last) {
            template += `<li class="page-item">
          <a class="page-link" href="#" aria-label="Next">
            <span aria-hidden="true">&raquo;</span>
            <span class="sr-only">${PaginationCaptions.NEXT}</span>
          </a>
        </li>`
        }

        this._pagination.append(template);
    }

    clearPagination() {
        this._pagination.html('');
    }

    clearSelectedPage() {
        this._selectedPage = 0;
    }

    clearExceptionMessage() {
        $('.jExceptionMessageText', this._modal).text('');
    }

    bindHandlers() {
        this._filterLogin
            .on('change', () => {
                this.clearSelectedPage();
                this.getOperationsList(true);
            });

        this._pagination
            .on('click', 'a[aria-label="Previous"]', (evt) => {
                evt.preventDefault();
                this._selectedPage = this._selectedPage - 1;
                this.getOperationsList(true);
            })
            .on('click', 'a[aria-label="Next"]', (evt) => {
                evt.preventDefault();
                this._selectedPage = this._selectedPage + 1;
                this.getOperationsList(true);
            })
            .on('click', 'a[aria-label="First"]', (evt) => {
                evt.preventDefault();
                this._selectedPage = 0;
                this.getOperationsList(true);
            });

        this._modal
            .on('hidden.bs.modal', () => {
                this.clearExceptionMessage();
            });

        this._pageSizeSelect
            .on('change', () => {
                this._pageSize = this._pageSizeSelect.val();
                this.clearSelectedPage();
                this.getOperationsList(true);
            })
    }

    onHide() {
        clearTimeout(this._timeoutHandler);
    }
}
